<script>
import imgtest from "@/Pages/Common/imgtes";
import Photograph from "@/Pages/Common/Photograph";
import Historys from "@/Pages/Common/Historygoods";
// import Vue from 'vue'
import {
  Noticedetail,
  sortingupload,
  search_detail,
  getWayParter,
  Carriers,
  History,
  GetUsableShelves,
  TakeGoods,
  GetInputID
} from "../../api";
import {
  TemplatePrint,
  GetPrinterDictionary,
  FilePrint,
  FormPhoto,
  SeletUploadFile,
  PictureShow
} from "@/js/browser.js";
let Base64 = require("js-base64").Base64;
let product_url = require("../../../static/pubilc.dev");
import $ from "jquery";
import Vue from "vue";
import moment from "moment";
// import _ from 'lodash'
// Vue.prototype._ = _
let lodash = require("lodash");

// var getuplode=""

export default {
  name: "RoutineEnter",
  components: {
    "img-test": imgtest,
    "photo-graph": Photograph,
    "Historys-dom": Historys
  },
  props: {
    fatherMethod: {
      type: Function,
      default: null
    }
  },
  data() {
    return {

      // getuplode:getuplode,
      // config:configs,
      // Budgetdetail:false, //
      TakeGoodsName: "",
      Conditionstype: true,
      printurl: product_url.pfwms,
      loading: true, //loading
      printlist: [], //
      Conveyingplace: [], //
      Conveyingplace2: [],
      ConveyingplaceID:"",//ID
      chengevalue: {
        inputval: "",
        value: "",
        type: ""
      },
      setClientCode: false, //
      ClientCode: "", //
      model11: "",
      sethousbox: false, //
      housenumber: "", //
      Storehouselist: [], //
      detail_ID: "",
      searchkey: "", //
      showphoto2: false, //,
      time: "",
      WarehousingMsg: false, // 
      Nomatching: [], //
      details: {
        //
        waybillinfo: {}, //
        wayBillID: "",
        total: 0,
        pageIndex: 1,
        pageSize: 10000,
        WaybillNo: "90416165067", //()
        Conveyorsite: "", //,
        columns1: [
          {
            type: "selection",
            width: 50,
            align: "center"
          },
          {
            title: "# ",
            slot: "indexs",
            align: "left",
            width: 30
            // fixed: 'right'
          },
          {
            title: "",
            key: "PartNumber",
            // width: 80,
            render: (h, params) => {
              var vm = this;
              if (params.row.iscx == false) {
                return h("span", {}, params.row.Product.PartNumber);
              } else {
                return h("Input", {
                  props: {
                    //Input
                    value: params.row.Product.PartNumber
                  },
                  on: {
                    "on-change"(event) {
                      //
                      //
                      params.row.Product.PartNumber = event.target.value;
                      vm.details.detailitem[params.index] = params.row;
                      var arr = vm.details.detailitem;
                      for (var i = 0; i < arr.length; i++) {
                        if (arr[i].PID == params.row.ID) {
                          arr[i].Product.PartNumber = event.target.value;
                        }
                      }
                      vm.clicktest(params.row);
                      // var cc=lodash.throttle(function(){
                      //       console.log('hello')
                      //       console.log(this)
                      // },1000)
                    }
                  }
                });
              }
            }
          },
          {
            title: "",
            key: "Manufacturer",
            // width: 80,
            render: (h, params) => {
              var vm = this;
              if (params.row.iscx == false) {
                return h("span", {}, params.row.Product.Manufacturer);
              } else {
                return h("Input", {
                  props: {
                    //Input
                    value: params.row.Product.Manufacturer
                  },
                  on: {
                    "on-change"(event) {
                      //
                      //
                      params.row.Product.Manufacturer = event.target.value;
                      vm.details.detailitem[params.index] = params.row;
                      var arr = vm.details.detailitem;
                      for (var i = 0; i < arr.length; i++) {
                        if (arr[i].PID == params.row.ID) {
                          arr[i].Product.Manufacturer = event.target.value;
                        }
                      }
                      vm.clicktest(params.row);
                    }
                  }
                });
              }
            }
          },
          {
            title: "/",
            slot: "Arrival",
            align: "center"
            // width: 70
          },
          {
            title: "",
            key: "DateCode",
            align: "center",
            // width: 60,
            render: (h, params) => {
              var vm = this;
              return h("Input", {
                props: {
                  //Input
                  value: params.row.Input.DateCode
                },
                on: {
                  "on-change"(event) {
                    //
                    //
