    finish_btn() {
      var isnull = null; // true  false 
      var that = this;
      var dataarr = this.details.detailitem;
      var null_num = 1;
      for (var i = 0; i < dataarr.length; i++) {
        if (dataarr[i].TruetoQuantity == "") {
          isnull = true;
          null_num++;
        } else {
          isnull = false;
          null_num = 1;
          // null_num=0
        }
      }

      // console.log(null_num)
      // if(isnull!=1){
      //   this.$Message.error("");
      // }
      function sum(arr) {
        return eval(arr.join("+"));
      }
      var arr = dataarr;
      var Nomatching = [];
      var map = {},
        dest = [];
      for (var i = 0; i < arr.length; i++) {
        var ai = arr[i];
        if (!map[ai.PID]) {
          dest.push({
            name: ai.Product.PartNumber,
            Quantity: ai.Quantity,
            PID: ai.PID,
            data: [Number(ai.TruetoQuantity)]
          });
          map[ai.PID] = ai.PID;
        } else {
          for (var j = 0; j < dest.length; j++) {
            var dj = dest[j];
            if (dj.PID == ai.PID) {
              dj.data.push(Number(ai.TruetoQuantity));
              break;
            }
          }
        }
      }

      for (var i = 0; i < dest.length; i++) {
        var item = dest[i];
        var total = sum(item.data);
        console.log(total);
        if (item.Quantity < total || item.Quantity > total) {
          Nomatching.push(item.name);
          // console.log(""+item.name+"  "+total)
        } else {
          // console.log(item.name+"   "+total)
        }
      }
      that.Nomatching = Nomatching;
      that.WarehousingMsg = true;
    },
