    Labelprint() {
      // 
      var arr = this.SelectRow;
      var printsrr = [];
      if (arr.length <= 0) {
        this.$Message.error("");
      } else {
        for (var i = 0; i < arr.length; i++) {
          var Inputs = arr[i].Inputs;
          var StandardProducts = arr[i].StandardProducts;
          var obj = {
            Quantity: arr[i].Quantity, //
            inputsID: arr[i].InputID, //id
            Catalog: arr[i].Product.Catalog, //
            PartNumber: arr[i].Product.PartNumber, //
            Manufacturer: arr[i].Product.Manufacturer, //
            Packing: arr[i].Product.Packing, //
            PackageCase: arr[i].Product.PackageCase, //
            origin: arr[i].Input.OriginDescription //
          };
          printsrr.push(obj);
        }
        var configs = GetPrinterDictionary();
        var getsetting = configs[""];
        // var href=window.location.protocol+"//"+window.location.host;
        // var newurl="http://hv.warehouse.b1b.com"+getsetting.Url
        // getsetting.Url=newurl

        var str = getsetting.Url;
        var testurl = str.indexOf("http") != -1;
        if (testurl == true) {
          getsetting.Url = getsetting.Url;
        } else {
          getsetting.Url = this.printurl + getsetting.Url;
        }

        var data = {
          setting: getsetting,
          data: printsrr
        };
        TemplatePrint(data);
      }
