          row.Product.PartNumber.indexOf(this.searchkey.toLowerCase()) !== -1 ||
          row.Product.Manufacturer.indexOf(this.searchkey.toUpperCase()) !== -1 ||
          row.Product.Manufacturer.indexOf(this.searchkey.toLowerCase()) !== -1
        ) {
          return "";
        } else {
